import React from "react";
import AMS from "./AMS.module.css"
//@ts-ignore
import {Card, Collapse, Container, Row, Col} from 'bootstrap-4-react'

const AboutMe = () => {
    return (<div className={AMS.marginAll}>

            <div className={AMS.header}>
                <h1>
                    About Me
                </h1>
            </div>
            <div className={AMS.aboutMeText}>
                Hi, my name is Vlad, I’m 22 years old,
                I’m learning the javascript programming language and its framework - React js.
                I like programming and I would like to find work in this area in order to further
                develop my programming abilities, perform interesting tasks every day,
                find like-minded people and just have interesting communication
            </div>
            <React.Fragment>
                <p>
                    <Container className={AMS.ButtonsPose}>
                        <Row>
                            <Col col="sm-4">
                                <Collapse.Button success as="a" target="#MyDreamJob" aria-expanded="false"
                                                 aria-controls="collapseExample">
                                    My dream job
                                </Collapse.Button>
                            </Col>
                            <Col col="sm-5">
                                <Collapse.Button danger  as="a" target="#DevelopmentExperience" aria-expanded="false"
                                                 aria-controls="collapseExample">
                                    My development experience
                                </Collapse.Button>
                            </Col>
                            <Col col="sm-2">
                                <Collapse.Button info as="a" target="#Contacts" aria-expanded="false"
                                                 aria-controls="collapseExample">
                                    My contacts
                                </Collapse.Button>
                            </Col>
                        </Row>
                    </Container>


                </p>

                <Collapse id="MyDreamJob">
                    <Card>
                        <Card.Body>
                            lol
                        </Card.Body>
                    </Card>
                </Collapse>
                <Collapse id="DevelopmentExperience">
                    <Card>
                        <Card.Body>
                            kek
                        </Card.Body>
                    </Card>
                </Collapse>
                <Collapse id="Contacts">
                    <Card>
                        <Card.Body>
                            kek
                        </Card.Body>
                    </Card>
                </Collapse>

            </React.Fragment>

        </div>
    )
}

export default AboutMe