import React from "react";
import {NavLink} from "react-router-dom";
import logo from "../../utils/imgs/react_logo.png"
//@ts-ignore
import {Navbar , Nav,  Collapse } from 'bootstrap-4-react';


const Header = ( ) =>{
    return(
        <Navbar expand="lg" dark bg="dark" mb="0">
            <Navbar.Brand href="#">
                <img src={logo} width="60" height="40" className="d-inline-block align-top" alt="logo"/>
            </Navbar.Brand>
            <Navbar.Toggler target="#navbarColor1" />
            <Collapse navbar id="navbarColor1">
                <Navbar.Nav mr="auto">
                 <NavLink to={"/Home/"}>
                     Home
                 </NavLink>
                    <NavLink to={"/AboutMe/"}>
                        AboutMe
                    </NavLink>
                </Navbar.Nav>
         
            </Collapse>
        </Navbar>
    )
}

export default Header